.jumbotron {
  padding: 4rem 0;

  @include has(header) {
    padding: 2rem 0;
  }

  @include has(title) {
    text-align: center;
    margin-bottom: 1rem;
    font-size: 5rem;
    font-weight: 700;
  }

  @include has(subtitle) {
    font-weight: 100;
    font-size: 2.5rem;
    display: block;
    text-align: center;
  }

  @include has(underline) {
    width: 200px;
    height: 2px;
    display: block;
    margin: 2.5rem auto;
  }

  @include has(body) {
    padding: 2rem 0;
  }

  @include variant(dark) {
    background-color: $primary_black;

    @include has(title) {
      color: $primary_white;
    }

    @include has(underline) {
      background-color: $primary_dark;
    }

    @include has(subtitle) {
      color: $primary_white;
    }
  }

  @include variant(light) {
    background-color: $primary_white;

    @include has(title) {
      color: $primary_black;
    }

    @include has(underline) {
      background-color: $primary_light;
    }

    @include has(subtitle) {
      color: $primary_black;
    }
  }
}